body {
    background-color: aliceblue;
  }
  
  .home__container {
    box-sizing: border-box;
  }
  
  .home__sidebar {
    margin-left: 0;
    width: 30%;
    height: 100vh;
    font-size: 1em;
    color: black;
    font-weight: 600;
    background-color: aliceblue;
    z-index: 1;
    position: fixed;
    border-right: 2px solid #ccc;
    overflow: auto;
  }
  
  .home__sidebar > h1 {
    font-weight: normal;
    font-size: 1.5rem;
  }
  
  .button__container {
    text-align: center;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
  }
  
  .sidebar__button {
    width: 60%;
    margin: 20px auto;
    top: 20px;
    font-size: 1.3vw;
    padding: 20px;
    border: none;
    border-bottom: 2px solid #ccc;
    background-color: aliceblue;
    outline: none;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px;
  }
  
  .button__in {
    background-color: inherit;
    border: none;
  }
  
  .sidebar__button:hover {
    background-color: rgb(0, 195, 255);
    color: white;
    outline: none;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
      rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
  }
  
  .home__row {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }
  
  .home__mainbar {
    margin-left: 30%;
    height: 100vh;
    font-size: 1em;
    color: black;
    overflow: auto;
    padding: 5px;
  }
  
  .home__mainbar > h1 {
    font-weight: 500;
    font-size: 2vw;
  }
  
  .mainbar__container {
    margin: 50px;
  }
  
  .searchbar_container {
    text-align: center;
  }
  
  @media (max-width: 700px) {
    .sidebar__button {
      width: 100%;
      margin: 15px auto;
      top: 20px;
      font-size: 0.6rem;
      padding: 20px;
      border: none;
      border-bottom: 2px solid #ccc;
      background-color: aliceblue;
      outline: none;
      box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px;
    }
  
    h1,
    h3 {
      font-size: 1.3rem;
    }
  }
  